import { HttpClient } from "./HttpClient"

export const createNewTicket = (data, token) => {
    const apiData = {
        method:"POST",
        url:`/conversation/message/new-ticket`,
        data:data,
        headers:{
            'Authorization':`Bearer ${token}`,
            'Content-Type': 'application/json'
        }
    }   
    return HttpClient.custom(apiData)
}

