export const getTicketConversation = (ticketId,token) => {
    const apiData = {
        method:"GET",
        url:`/conversation/${ticketId}/customer`,
        headers:{
            'Authorization':`Bearer ${token}`,
            'Content-Type': 'application/json'
        }
    }   
    return HttpClient.custom(apiData) 

}

