export function debounceWithImmediate(func, immediateFunc, delay) {
    let timeoutId;
    let immediateCall = true;
    return  (props) => {
      clearTimeout(timeoutId);
      if (immediateCall) {
        immediateFunc(props)
        immediateCall = false;
      }
      timeoutId = setTimeout(() => {
        immediateCall = true;
        func(props)
      }, delay);
    }
  }
