import MainProvider from "../context/MainProvider";
import SocketProvider from "../context/SocketProvider";
import { HttpClient } from "../services/HttpClient";
import PullseChatbot from "./chatbot/Chatbot";
import { get } from 'lodash'

import React from 'react'

const PullseChatWidget = ({data, style}) => {
    console.log(data,"dataTokens")
    let token  = get(data,'token',"")
  return (
   <>
   <MainProvider  data={data} >
   <SocketProvider data={data}  >
    <PullseChatbot style={style} data={data} />
   </SocketProvider>
   </MainProvider>
   </>
  )
}


export {
    PullseChatWidget
}