import React, { Component, createContext, createRef, useCallback, useContext, useEffect, useRef, useState } from "react";
import io from "socket.io-client";
import axios from "axios";
import { get } from "lodash";
import useStateCallback from "../utils/hooks/useStateCallback";
import { HttpClient } from "../services/HttpClient";
import { chatbotSettingApi, customerAllTickets } from "../services/Apis"

export const MainContext = createContext({});
const MainProvider  = ({children,data}) => {
    const [customerTickets, setCustomerTickets] = useState([])
    const [chatbotSettingData, setChatbotSettingData] = useState({
      "chatbotSettings": {
          "imageLink": "https://pullseaipublicassets.blob.core.windows.net/pullse-ai-public-assets/messenger/chatbot-icon.png",
          "name": "Pullse AI"
      },
      "brandSettings": {
          "actionColor": "#A84ACB",
          "backgroundColor": "#A84ACB"
      },
      "launcherSettings": {
          "imageLink": "https://pullseaipublicassets.blob.core.windows.net/pullse-ai-public-assets/messenger/launcher-icon.png",
          "sideSpacing": "100",
          "bottomSpacing": "100"
      },
      "formBuilding": {
          "state": false
      }
  });
 

    const handleGetCustomerAllTickets = () => {
        customerAllTickets(data.token).then(res=>{
            let response = get(res,'data.data.docs',[])
            setCustomerTickets(response);
        })
    }
    useEffect(()=>{
        handleGetCustomerAllTickets();
        handleGetChatbotSetting();
     
    },[])

    const handleGetChatbotSetting = () => {
      chatbotSettingApi(data.token).then(res=>{
        let response = get(res,'data.data',{})
        if(Object.keys(response).length){
          setChatbotSettingData(response);
        }
      })
      
    }
    
    return (
      <>
        <MainContext.Provider value={{
            customerTickets,
            handleGetCustomerAllTickets,
            chatbotSettingData
        }}>
          {children}
        </MainContext.Provider>
      </>
    );
}

export default MainProvider;
